



































































































































import Vue from 'vue'

export default Vue.extend({
  methods: {
    printme() {
      let f = <any>document.getElementById('printme')
      if (f != null) {
        f.contentWindow.print()
      }
    } 
  },
  mounted() {
    let f = <any>document.getElementById('printme')
    if (f != null) {
      f.contentDocument.write(`
        <link href="/assets/styles.16070.css" rel="stylesheet">        
        <style>
          .resume-header {
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-between;
            border-bottom: 1px solid #ccc;
            margin-bottom: 20px;
          }
          .contact li{
            list-style: none;
            line-height: 30px;
            text-align: left;
          }
          .contact a:hover, a:visited, a:link, a:active {
            text-decoration: none;            
            color: #000;            
          }
          .contact li i{
            font-size: 18px
          }
          .title {
            padding-bottom: 5px;
            border-bottom: 1px solid #ccc;
          }
          .resume-body {
            font-family: "Lucida Grande","Helvetica Neue",Helvetica,Arial,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","WenQuanYi Zen Hei","WenQuanYi Micro Hei","Noto Sans CJK SC","Source Han Sans CN",SimSun,sans-serif;
            width: 60%;            
            margin-top: 20px;
            color: #333;
            background: #fff;
            padding: 5% 5%;
          }
        </style>
      ` + (<any>this.$refs.resumeBody).innerHTML)
      f.contentDocument.close()    
    }
  }
})
