































import Vue from 'vue'
import { login, authorizationCheck } from '../api'
export default Vue.extend({
  data: (): {
    logedin: boolean,
    username: string,
    password: string
  } => ({
    logedin: false,
    username: "",
    password: ""
  }),
  methods: {
    login() {
      login({username: this.username, password: this.password})
      .then(res => res.data)
      .then(data => {
        if (data.result == true) {
          this.$$app.logedin = true
          this.$router.push(this.$route.query.redirect || '/')
          window.sessionStorage.clear()
        }
      })
    }
  },
  created() {
    authorizationCheck()
    .then(res => res.data)
    .then(data => {
      if (data.result == true) {
        this.logedin = true 
        this.$$app.logedin = true
        setTimeout(() => this.$router.push('/'), 2000)
      }
    })
  }
})
